import {
  Component,
  OnInit} from '@angular/core';

import { AnalyticsService } from '../analytics.service';

const resume: string = 'https://docs.google.com/document/d/1WDTFXYEEXIcaYurEHhBO0z5zBpj7OutjGgFssJ6QVSo/edit?usp=sharing';

@Component({
  selector: 'app-cv',
  templateUrl: './cv.component.html',
  styleUrls: ['./cv.component.scss'],
})
export class CvComponent implements OnInit {

  constructor(private analytics: AnalyticsService) { }

  downloadCV(): void {
    this.analytics.eventEmitter('open_resume', 'cv', 'click', 'resume_google_docs');
    window.open(resume);
  }

  openURL(site: string, url: string): void {
    this.analytics.eventEmitter(`open_url_${site}`, 'cv', 'click', `${site}`);
    window.open(url);
  }

  ngOnInit() {
  }

}
