import {
  Component,
  ViewEncapsulation,
  AfterViewInit } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';

import * as devtools from '../../node_modules/devtools-detect/index';

declare let gtag: Function;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class AppComponent implements AfterViewInit {

  constructor(public router: Router) {
      this.router.events.subscribe( event => {
        if (event instanceof NavigationEnd) {
          gtag('config', 'xx-xxxxx-xx',
                   { page_path: event.urlAfterRedirects }
          );
        }
      });
  }

  title: string = 'Michael Wood';
  routerLinks = [
    { path: 'home', label: 'home' },
    { path: 'open-source', label: 'open-source' },
    { path: 'resume', label: 'resume' },
    // { path: 'contact-me', label: 'contact-me' },
  ];

  ngAfterViewInit(): void {
    // window.addEventListener('devtoolschange', event => {
      // console.warn(`you cheeky devil`, event.detail.isOpen);
      // });
      console.warn(`you cheeky devil`);

  }

}
